import { createRoot } from "react-dom/client";
import { ConvexProvider } from "convex/react";
import { ConvexReactClient } from "convex/react";
import "./index.css";
import App from "./App";
import { initializeDarkMode } from "./darkMode";

// Ensure we have the Convex URL
const convexUrl = "https://perfect-mule-434.convex.cloud";

console.log("Connecting to Convex at:", convexUrl);
const convex = new ConvexReactClient(convexUrl);

// Initialize dark mode before rendering
initializeDarkMode();

const root = createRoot(document.getElementById("root")!);

// Handle connection errors
window.addEventListener('unhandledrejection', (event) => {
  if (event.reason?.message?.includes('Failed to fetch')) {
    console.error('Connection error:', event.reason);
    root.render(
      <div className="flex items-center justify-center min-h-screen bg-gray-100">
        <div className="p-8 bg-white rounded-lg shadow-md">
          <h2 className="text-xl font-semibold text-red-600 mb-4">Connection Error</h2>
          <p className="text-gray-600">Unable to connect to the server. Please check your internet connection and try again.</p>
        </div>
      </div>
    );
  }
});

root.render(
  <ConvexProvider client={convex}>
    <App />
  </ConvexProvider>
);
